import Vue from 'vue'
import Router from 'vue-router'
import triangleGenerator from '@/components/triangleGenerator'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/demo/triangle',
      name: 'triangleGenerator',
      component: triangleGenerator
    }
  ]
})



