<template lang="html">
  <div class="triangle-generator container">
    <header>
      <h1 class="title is-1">CSS三角形生成器</h1>
    </header>

    <article class="message">
      <div class="message-header">
        <p>Vue</p>
      </div>
      <div class="message-body">
        <div class="columns">
          <aside class="sidebar column is-half">
            <div class="field is-horizontal direction">
              <div class="field-label">
                <label class="label">箭头方向</label>
              </div>
              <div class="field-body">
                <div class="field">
                  <div class="control not-rotate">
                      <label v-for="dir in specialDir" :for="dir" class="radio" :id="dir + '-label'" :class="{active: dir === direction}">
                        <input type="radio" name="direction" :id="dir" :value="dir" v-model="direction">
                      </label>
                  </div>

                  <div class="control rotate">
                    <label v-for="dir in normalDir" :for="dir" class="radio" :id="dir + '-label'" :class="{active: dir === direction}">
                      <input type="radio" name="direction" :id="dir" :value="dir" v-model="direction">
                    </label>
                  </div>

                  <div class="centerFill"></div>
                </div>
              </div>
            </div>

            <div class="field is-horizontal">
              <div class="field-label">
                <label class="label">类型</label>
              </div>
              <div class="field-body">
                <div class="field is-narrow">
                  <div class="control">
                    <label for="iso" class="radio">
                      <input type="radio" id="iso" name="type" value="iso" v-model="type" :disabled="isSpecialDir">
                      等腰
                    </label>

                    <label for="equ" class="radio">
                      <input type="radio" id="equ" name="type" value="equ" v-model="type" :disabled="isSpecialDir">
                      等边
                    </label>
                  </div>

                </div>
              </div>
            </div>

            <div class="field is-horizontal align-center">
              <div class="field-label is-normal">
                <label class="label">宽</label>
              </div>
              <div class="field-body">
                <div class="field has-addons">
                  <p class="control is-expanded">
                    <input type="number" v-model="width" class="input">
                  </p>
                  <p class="control"><span class="button">px</span>
                  </p>
                </div>
              </div>
            </div>

            <div class="field align-center has-addons">
              <div class="field-label is-normal">
                <label class="label">高</label>
              </div>
              <div class="field-body">
                <div class="field has-addons">
                  <p class="control is-expanded">
                    <input type="number" v-model="height" class="input" placeholder="高度" :disabled="isEquType && !isSpecialDir">
                  </p>
                  <p class="control"><span class="button" v-if="!isEquType">px</span></p>
                </div>
              </div>
            </div>
            <div class="field is-horizontal">
              <div class="field-label is-normal">
                <label class="label">颜色</label>
              </div>
              <div class="field-body">
                <div class="field">
                  <div class="color-picker">
                    <chrome-picker v-model="pickedColors" @change-color="onChangeColorPicker"></chrome-picker>
                  </div>
                </div>

              </div>
            </div>
          </aside>
          <div class="workArea column">
            <h2 class="title">预览</h2>
            <div class="triangleOutput">
              <div class="triangle" ref="triangle" :style="{
                borderTopColor: styleObject.borderColor.top,
                borderRightColor: styleObject.borderColor.right,
                borderBottomColor: styleObject.borderColor.bottom,
                borderLeftColor: styleObject.borderColor.left,
                borderTopWidth: styleObject.borderWidth.top,
                borderRightWidth: styleObject.borderWidth.right,
                borderBottomWidth: styleObject.borderWidth.bottom,
                borderLeftWidth: styleObject.borderWidth.left
                }"></div>
            </div>
            <div class="cssOutput">
              <h2 class="title">CSS</h2>
              <figure class="highlight">
<pre ref="css">
.triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width:{{code.width}};
  border-color:{{code.color}};
}

</pre>
              </figure>
            </div>
          </div>
        </div>
      </div>
    </article>

  </div>
</template>

<script>
import { Chrome } from 'vue-color'
let defaultProps = {
  hex: '#194d33',
  hsl: {
    h: 150,
    s: 0.5,
    l: 0.2,
    a: 1
  },
  hsv: {
    h: 150,
    s: 0.66,
    v: 0.30,
    a: 1
  },
  rgba: {
    r: 25,
    g: 77,
    b: 51,
    a: 1
  },
  a: 1
}
const colorDirection = {
  'top': 'bottom',
  'right': 'left',
  'bottom': 'top',
  'left': 'right',
  'left-top': 'top',
  'right-top': 'top',
  'right-bottom': 'bottom',
  'left-bottom': 'bottom'
}
const lengthsDirection = {
  'top': {
    'top': false,
    'left': 'width-half',
    'bottom': 'height',
    'right': 'width-half'
  },
  'right': {
    'top': 'height-half',
    'right': false,
    'bottom': 'height-half',
    'left': 'width'
  },
  'bottom': {
    'top': 'height',
    'right': 'width-half',
    'bottom': false,
    'left': 'width-half'
  },
  'left': {
    'top': 'height-half',
    'right': 'width',
    'bottom': 'height-half',
    'left': false
  },
  'left-top': {
    'top': 'height',
    'right': 'width',
    'bottom': false,
    'left': false
  },
  'right-top': {
    'top': 'height',
    'right': false,
    'bottom': false,
    'left': 'width'
  },
  'right-bottom': {
    'top': false,
    'right': false,
    'bottom': 'height',
    'left': 'width'
  },
  'left-bottom': {
    'top': false,
    'right': 'width',
    'bottom': 'height',
    'left': false
  }
}
// const specialDir = ['left-top', 'right-top', 'right-bottom', 'left-bottom']

export default {
  data () {
    return {
      direction: 'top',
      type: 'iso',
      pickedColors: defaultProps,
      width: 100,
      height: 100,
      styleObject: {
        borderWidth: {
          top: '',
          right: '',
          bottom: '',
          left: ''
        },
        borderColor: {
          top: '',
          right: '',
          bottom: '',
          left: ''
        }
      },
      code: {
        width: '',
        color: ''
      },
      normalDir: ['top', 'right', 'bottom', 'left'],
      specialDir: ['left-top', 'right-top', 'right-bottom', 'left-bottom']
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.updateColorCSS()
      this.updateWidthCSS()
    })
  },
  methods: {
    onChangeColorPicker (val) {
      this.pickedColors = val
      this.updateColorCSS()
    },
    updateColorCSS () {
      this.code.color = ''
      let borderColor = this.styleObject.borderColor
      for (let i in borderColor) {
        borderColor[i] = 'transparent'
      }
      borderColor[colorDirection[this.direction]] = this.pickedColors.hex
      for (let i in borderColor) {
        if (i === 'left' && borderColor[i] === borderColor.right) {
          break
        }
        this.code.color += ' ' + borderColor[i]
      }
    },
    updateWidthCSS () {
      this.code.width = ''
      this.isSpecialDir ? this.type = 'iso' : null
      let direction = this.direction
      let type = this.type
      let borderWidth = this.styleObject.borderWidth
      let width = this.width
      let height = this.height
      for (let i in lengthsDirection[direction]) {
        if (lengthsDirection[direction][i] === false) {
          borderWidth[i] = 0
        } else {
          switch (lengthsDirection[direction][i]) {
            case 'width':
              borderWidth[i] = width + 'px'
              break
            case 'width-half':
              borderWidth[i] = width / 2 + 'px'
              break
            case 'height':
              borderWidth[i] = height + 'px'
              break
            case 'height-half':
              borderWidth[i] = height / 2 + 'px'
              break
          }
          if (type === 'equ') {
            let equHeight = (Math.sqrt(3) / 2 * width).toFixed(1)
            switch (lengthsDirection[direction][i]) {
              case 'height':
                borderWidth[i] = equHeight + 'px'
                break
              case 'height-half':
                borderWidth[i] = equHeight / 2 + 'px'
                break
            }
          }
        }
      }
      // 格式化并缩写要出去的CSS代码
      for (let i in borderWidth) {
        if (i === 'left' && borderWidth[i] === borderWidth.right) {
          break
        }
        this.code.width += ' ' + borderWidth[i]
      }
    }
  },
  computed: {
    isEquType: function () {
      return this.type === 'equ'
    },
    isSpecialDir: function () {
      return this.specialDir.some((item) => {
        return this.direction === item
      })
    }
  },
  watch: {
    direction: function () {
      // this.isSpecialDir && this.isEquType ? this.type = 'iso' : null
      this.updateColorCSS()
      this.updateWidthCSS()
    },
    width: 'updateWidthCSS',
    height: 'updateWidthCSS',
    type: 'updateWidthCSS'
  },
  components: {
    'chrome-picker': Chrome
  }
}
</script>

<style lang='stylus' rel="stylesheet/stylus">
sqrt(x)
  return math(x, 'sqrt')
.align-center
  align-items center
.field-label
  padding-top 0 !important
header
  margin 1.5em auto
  text-align center
  // font-size 30px
.direction
  input[type = "radio"]
    display none
  .field
    position relative
    width 100%
    height 70px
    .centerFill
      position absolute
      top 12px
      left @top
      width 38px
      height @width
      background #fff
    .not-rotate
      width 60px
      height @width
      label
        width 30px
        height @width
        background #194d33
    .rotate
      width 54px
      height @width
      margin 4px 0 0 4px
      transform rotate(45deg)
      label
        width 27px
        height @width
        background lighten(#194d33, 30)
    .control
      position absolute
      top 0
      left 0
      label
        position absolute
        margin-left 0
        transition all .4s ease
        &.active
          background #e96900
        &:hover
          background #e96900
        &#top-label, &#left-top-label
          top 0
          left 0
        &#right-label, &#right-top-label
          top 0
          right 0
        &#bottom-label, &#right-bottom-label
          bottom 0
          right 0
        &#left-label, &#left-bottom-label
          left 0
          bottom 0
.color-picker
    display inline-block
.workArea
  .triangleOutput
    display flex
    align-items center
    min-height 15em
    overflow auto
    background #fff
    background-image linear-gradient(45deg, #bbb 25%, transparent 0),
                     linear-gradient(45deg, transparent 75%, #bbb 0),
                     linear-gradient(45deg, #bbb 25%, transparent 0),
                     linear-gradient(45deg, transparent 75%, #bbb 0)
    background-position 0 0, 15px 15px, 15px 15px, 30px 30px
    background-size 30px 30px
    .triangle
      margin 0 auto
      width 0
      height 0
      border-style solid
      border-width 100px
      transition all .4s ease
  .cssOutput
    margin-top 1.5em
    pre
      padding .2em 1.4em
      font-family Consolas
      background lightgray
      overflow-x auto
</style>



